import React, {useState} from "react";
import useSound from "use-sound";
import correctPLaySound from "./sounds/correct-answer.mp3";
import wrongPLaySound from "./sounds/wrong-answer.mp3";
import completedPLaySound from "./sounds/completed.mp3";
import {ToastContainer, toast} from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import questions from "./question";

export default function App() {
  //shuffled Array
  questions.sort(() => 0.5 - Math.random());
  const [correctPLay] = useSound(correctPLaySound);
  const [wrongPLay] = useSound(wrongPLaySound);
  const [completedPLay] = useSound(completedPLaySound);

  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [showScore, setShowScore] = useState(false);
  const [score, setScore] = useState(0);

  const handleAnswerOptionClick = (isCorrect) => {

    if (isCorrect) {
      correctPLay()
      toast.success("Correct", {
      autoClose: 1000,
    });
      setScore(score + 1);
    } else {
       wrongPLay()
       toast.error("Wrong", {
      autoClose: 1000,
    })
    }

    const nextQuestion = currentQuestion + 1;
    if (nextQuestion < 5) {
      setCurrentQuestion(nextQuestion);
    } else {
      completedPLay()
      toast.info("Quiz completed", {
        position: "top-center",
      });
      setShowScore(true);
    }
  };

  return (
    <div className='app'>
      <ToastContainer />
      {showScore ? (
        <div className='score-section'>
          You scored {score} out of {5}
        </div>
      ) : (
        <>
          <div className='question-section'>
            <div className='question-count'>
              <span>Question {currentQuestion + 1}</span>/{5}
            </div>
            <div className='question-text'>
              {questions[currentQuestion].questionText}
            </div>
          </div>
          <div className='answer-section'>
            {questions[currentQuestion].answerOptions.map((answerOption) => (
              <button
                onClick={() => handleAnswerOptionClick(answerOption.isCorrect)}>
                {answerOption.answerText}
              </button>
            ))}
          </div>
        </>
      )}
    </div>
  );
}
